import {Component, ElementRef, EventEmitter, Inject, Input, OnInit, Output} from "@angular/core";

/**
@deprecated This component will be removed in the future. Use DayPilot.Modal instead.
 */
@Component({
  selector: 'daypilot-modal',
  styles: [`
    .modal {
      position: fixed;
      top: 0px;
      left: 0px;
      right: 0px;
      max-height: 80%;
      overflow-y: auto;
      padding: 20px;
      background-color: white;

      transform: translateY(-100%);
    }

    .modal.visible {
      transform: translateY(0);
    }

    .overlay {
      position: fixed;
      top: 0px;
      left: 0px;
      right: 0px;
      bottom: 0px;
      background-color: #000;

      opacity: 0;
      margin-left: 100%;
    }

    .overlay.visible {
      margin-left: 0;
      opacity: 0.3;
    }

    .modal {
      transition: transform 0.3s cubic-bezier(0, 0, 0.3, 1);
    }

    .overlay {
      transition: margin-left 0s linear 0.3s, opacity 0.3s;
    }

    .overlay.visible {
      transition: margin-left 0s, opacity 0.3s;
    }
  `],
  template: `
    <div
      class="overlay"
      (click)="hide()"
      [class.visible]="visible"
    ></div>
    <div
      class="modal"
      [class.visible]="visible"
    >
      <ng-content></ng-content>
    </div>`,
})
export class DayPilotModalComponent implements OnInit {
  @Input() autoFocus: boolean = true;
  @Output() close: EventEmitter<any> = new EventEmitter();

  private _visibility: "visible" | "hidden" = "hidden";

  constructor(@Inject(ElementRef) private element: ElementRef) {
  }

  get visible(): boolean {
    return this._visibility === "visible";
  }

  ngOnInit() {
  }

  show() {
    this._visibility = "visible";

    let element = this.element.nativeElement;

    if (this.autoFocus) {
      setTimeout(() => {
        let first = element.querySelector("input");
        first && first.focus();
      });
    }
  }

  hide(result?: any) {
    this._visibility = "hidden";
    this.close.emit({"result": result});
  }
}
